@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
body{height:100vh;font-family:"Righteous", cursive;margin:0}.root{display:flex;flex-direction:column;justify-content:center;height:100%;background:linear-gradient(90deg, #b7f8db 0%, #50a7c2 100%)}.counter{display:flex;flex-direction:column;align-items:center;margin:auto;grid-gap:10px;gap:10px}.counter__header{margin:0}.counter__number{margin:0;font-size:100px;font-weight:bold}.counter__buttons-container{display:flex;flex-direction:row;grid-gap:10px;gap:10px}.counter__button{padding:15px;font-size:1.1em;font-family:inherit;background-color:#0000;border:1px solid black;transition:0.2s}.counter__button:hover{background-color:#fcb69ff0}.counter__button:active{background-color:#0000}

